
#root{
	padding: auto;
}
.body {
	padding: 0;
	min-height: 90vh;
}
.card-login{
	min-width: 300px;
	max-width: 400px;
	background-color: #fff;
	background-clip: border-box;
	border: 1px solid rgba(0,0,0,.125);
	border-radius: .25rem;
	margin: 0 auto;
	margin-top: 15vh;
	margin-bottom: 25vh;
	padding-top: 1.25vw;
}

.card-otp{
	min-width: 300px;
	max-width: 400px;
	background-color: #fff;
	background-clip: border-box;
	border: 1px solid rgba(0,0,0,.125);
	border-radius: .25rem;
	margin: 0 auto;
	margin-top: 25vh;
	margin-bottom: 25vh;
}

.card-body {
	padding: 1.25vw;
}

.card-form-body {
	padding: 0.50vw 0.50vw 0.50vw 0vw;
	background-color: #fff;
	background-clip: border-box;
}
.row{
	margin: 0;
}

.forgetPassword{
	text-align: center;
}

.inputField{
	width: 100%;
}
.input{
	width: 100%;
	padding: 8px 6px;
	margin: 0px 0;
	display: inline-block;
	border: 1px solid #ccc;
	box-sizing: border-box;
}

.form-input {
	font-size:16px;
	margin-bottom: 0.5rem;
	padding:1px 1px 1px 1px;
	display:block;
	width:100%;
	border:none;
	border-bottom:1px solid #757575;
}

.form-input:focus{
	outline: none;
	border-bottom:2px solid #0f0f0f;
}
.form-input-label{
	width: 35vh;
	color: black;
}

.form-dropdown-select {
	font-size:16px;
	padding:1px 1px 1px 1px;
	display:block;
	width:100%;
	border:none;
	border-bottom:1px solid #757575;
}
.form-dropdown{
	margin-bottom: 0.5rem;
	color: #ffffff;
	overflow: hidden;
	cursor: pointer;
	width: 100%;
}

.form-dropdown:focus{
	outline: none;
	border-bottom:2px solid #0f0f0f;
}

.form-dropdown:focus{
	outline: none;
	border-bottom:2px solid #0f0f0f;
}
.checkbox-input{
	box-sizing: border-box;
	padding: 0;
	margin-top: 6px;
}

.navbar {
	padding: 0.25;
	background-color: #6B4C62;
	width: 100%;
	min-height: 50px;
	display: inline-block;
}


.mydiv	{
	background-color: #6B4C62;
	color: #ffffff;
	font-size: 16px;
	text-align: center;
	text-decoration: none;
	border-width: 2px; 
	margin-left: 1.75rem;
} 

.mydiv:hover{
	background-color: #5e4256;
	border-bottom:2px solid #ffffff;
}
.mydiv a {
	text-decoration: none;
	color: #ffffff;
} 

.dropdown {
	color: #ffffff;
	overflow: hidden;
	cursor: pointer;
}
/* .navbar a:hover, .dropdown:hover .dropbtn {
	background-color: red;
} */

.dropdown-select {
	width: 100%;
	padding: 8px 6px;
	margin: 0px 0;
	display: inline-block;
	border: 1px solid #ccc;
	box-sizing: border-box;
}

.dropdown-content {
	display: none;
	position: fixed;
	background-color: #6B4C62;
	min-width: 10vw;
	z-index: 1;
}

.dropdown-content a {
	float: none;
	color: #ffffff;
	padding: 10px 14px;
	text-decoration: none;
	display: block;
}

/* .dropdown-content a:hover {
	background-color: #ddd;
} */

.dropdown:hover .dropdown-select {
	display: block;
}

.footer{
	right: 0;
	bottom: 0;
	left: 0;
	min-height: fit-content;
	width: 100%;
	color: #ffffff;
	background-color: #6B4C62;
}

.btn{
	width: 100%;
	padding: 12px;
	color: #ffffff;
	background-color: #039c9a;
	border-color: #00a09d;
	border-radius: 0.125rem;
	border: 1px solid transparent;
	padding: 0.375rem 0.75rem;
	margin: 5px 0;
	opacity: 0.85;
	display: inline-block;
	font-size: 17px;
	line-height: 20px;
	text-decoration: none;
}

.btn:disabled {
	opacity: 0.65;
	cursor: not-allowed;
}
.btn:hover{
	color:#000000;
}

.maxisFieldset1 .p-fieldset-legend-text{
	font-weight: bold;
	font-size: 1.3rem;
	text-decoration: underline;
	color: #000077;
}
